let getters = {

}
let mutations = {
  push(state,m){
    if(localStorage.xgwc){
      state.gwc = [...JSON.parse(localStorage.xgwc)]
    }
    if(state.gwc.length==0){
      state.gwc.push({...m,num:0})
    }
    let type = true
    for(let i=0;i<state.gwc.length;i++){
        if(state.gwc[i].m == m.m){
          type = false
          state.gwc[i].num++
        }
    }
    if(type){
      state.gwc.push({...m,num:1})
    }
    localStorage.xgwc = JSON.stringify(state.gwc)
  },
  jia(state,m){
    // mutations.push(state,m)
    if(localStorage.xgwc){
      state.gwc = [...JSON.parse(localStorage.xgwc)]
    }
    let type = true
    for(let i=0;i<state.gwc.length;i++){
      if(m.m == state.gwc[i].m){
        type = false
        state.gwc[i].num++
      }
    }
    if(type){
      state.gwc.push({...m,num:1})
    }
    localStorage.xgwc = JSON.stringify(state.gwc)
  },
  jian(state,m){
    
    if(localStorage.xgwc){
      state.gwc = [...JSON.parse(localStorage.xgwc)]
    }
    for(let i=0;i<state.gwc.length;i++){
      if(m.m == state.gwc[i].m){
        state.gwc[i].num--
      }
      if(state.gwc[i].num <= 0){
          state.gwc[i].num = 0
          state.gwc.splice(i,1)
      }
    }
    localStorage.xgwc = JSON.stringify(state.gwc)
  }
}
let actions = {
  push({commit},m){
    commit('push',m)
  },
  jia({commit},m){
    commit('jia',m)
  },
  jian({commit},m){
    commit('jian',m)
  }

}

export default new Vuex.Store({
  state,
  getters,
  mutations,
  actions
})